/**
 * 
 * 
 * @interface
 * @prop {number} UNKNOWN 
 * @prop {number} ADDRESS_INVALID 
 * @prop {number} STATE_ERROR 
 * @prop {number} SOCKET_FAILED Socket 
 * @prop {number} BIND_FAILED 
 * @prop {number} LISTEN_FAILED 
 * @prop {number} ACCEPT_FAILED Accept 
 * @prop {number} CONNECT_FAILED 
 * @prop {number} CONNECT_TIMEOUT 
 * @prop {number} WRITE_TIMEOUT 
 * @prop {number} READ_TIMEOUT 
 * @prop {number} WRITE_FAILED 
 * @prop {number} READ_FAILED 
 * @prop {number} WRITE_OUTOFBOUNDS 
 */
export const MessageErrorCode = {

    /**  */
    UNKNOWN: 100,

    /**  */
    ADDRESS_INVALID: 101,
    /**  */
    STATE_ERROR: 102,

    /** Socket  */
    SOCKET_FAILED: 200,
    /**  */
    BIND_FAILED: 201,
    /**  */
    LISTEN_FAILED: 202,
    /** Accept  */
    ACCEPT_FAILED: 203,

    /**  */
    CONNECT_FAILED: 300,
    /**  */
    CONNECT_TIMEOUT: 301,

    /**  */
    WRITE_TIMEOUT: 401,
    /**  */
    READ_TIMEOUT: 402,
    /**  */
    WRITE_FAILED: 403,
    /**  */
    READ_FAILED: 404,
    /**  */
    WRITE_OUTOFBOUNDS: 405
}
