/**
 * 
 * @readonly
 * @enum
 * @property {number} Unknown 
 * @property {number} Unsent 
 * @property {number} Sending 
 * @property {number} Sent 
 * @property {number} Read 
 * @property {number} Recalled 
 * @property {number} Deleted 
 * @property {number} SendBlocked 
 * @property {number} ReceiveBlocked 
 * @property {number} Fault 
 */
export const MessageState = {

    /**
     * 
     * @type {number}
     */
    Unknown: 0,

    /**
     * 
     * @type {number}
     */
    Unsent: 5,

    /**
     * 
     * @type {number}
     */
    Sending: 9,

    /**
     * 
     * @type {number}
     */
    Sent: 10,

    /**
     * 
     * @type {number}
     */
    Read: 20,

    /**
     * 
     * @type {number}
     */
    Retracted: 30,

    /**
     * 
     * @type {number}
     */
    Deleted: 40,

    /**
     * 
     * @type {number}
     */
    SendBlocked: 51,

    /**
     * 
     * @type {number}
     */
    ReceiveBlocked: 52,

    /**
     * 
     * @type {number}
     */
    Fault: 1
}
