import cell from "@lib/cell-lib";

/**
 * 
 */
export class Packet {

    /**
     * 
     * @param {string} name 
     * @param {object} data 
     * @param {number} [sn] 
     */
    constructor(name, data, sn) {
        /**
         * 
         * @type {string}
         */
        this.name = name;

        /**
         * 
         * @type {object}
         */
        this.data = data;

        /**
         * 
         * @type {number}
         */
        if (undefined === sn) {
            this.sn = cell.Utils.generateSerialNumber();
        }
        else {
            this.sn = sn;
        }

        /**
         * 
         * @private
         * @type {object}
         */
        this.state = null;

        /**
         * 
         * @type {string}
         */
        this.responseType = 'json';

        /**
         * 
         * @type {number}
         */
        this.responseTimeout = 0;
    }

    /**
     * 
     * @returns {number} 
     */
    getStateCode() {
        if (null != this.state) {
            return this.state.code;
        }

        return -1;
    }

    /**
     * 
     * @returns {JSON} 
     */
    getPayload() {
        return this.data;
    }

    /**
     * 
     * @returns {number}
     */
    extractServiceStateCode() {
        return this.data.code;
    }

    /**
     * 
     * @returns {JSON}
     */
    extractServiceData() {
        return this.data.data;
    }

    /**
     *  JSON  {@link Packet} 
     * @param {JSON} json  JSON 
     * @returns {Packet}  {@link Packet} 
     */
    static create(json) {
        let packet = new Packet(json.name, json.data, json.sn);
        return packet;
    }
}
