/**
 *  Map 
 */
export class OrderMap {

    /**
     * 
     */
    constructor() {
        // 
        this._keys = [];
        // 
        this._values = [];
    }

    /**
     *  Map 
     * 
     * @returns {boolean}  {@linkcode true} 
     */
    isEmpty() {
        return this._keys.length == 0;
    }

    /**
     * 
     * 
     * @param {*} key 
     * @returns {boolean}  {@linkcode true}  {@linkcode false} 
     */
    containsKey(key) {
        return this._keys.indexOf(key) >= 0;
    }

    /**
     * 
     * 
     * @param {*} value 
     * @returns {boolean}  {@linkcode true}  {@linkcode false} 
     */
    containsValue(value) {
        return this._values.indexOf(value) >= 0;
    }

    /**
     *  Map 
     * 
     * @param {*} key 
     * @param {*} value 
     */
    put(key, value) {
        let index = this._keys.indexOf(key);
        if (index >= 0) {
            this._values[index] = value;
        }
        else {
            this._keys.push(key);
            this._values.push(value);
        }
    }

    /**
     * 
     * 
     * @param {*} key 
     * @returns {*}  {@linkcode null} 
     */
    get(key) {
        let index = this._keys.indexOf(key);
        if (index >= 0) {
            return this._values[index];
        }

        return null;
    }

    /**
     * 
     * 
     * @param {*} 
     * @returns {*}  {@linkcode null} 
     */
    remove(key) {
        let index = this._keys.indexOf(key);
        if (index >= 0) {
            let value = this._values[index];
            this._keys.splice(index, 1);
            this._values.splice(index, 1);
            return value;
        }

        return null;
    }

    /**
     *  Map 
     * 
     * @returns {Array} 
     */
    values() {
        let _values = new Array();
        Array.prototype.push.apply(_values, this._values);
        return _values;
    }

    /**
     *  Map 
     * 
     * @returns {Array} 
     */
    keys() {
        let _keys = new Array();
        Array.prototype.push.apply(_keys, this._keys);
        return _keys;
    }

    /**
     *  Map 
     * 
     * @returns {Array} 
     */
    keySet() {
        let _keys = new Array();
        Array.prototype.push.apply(_keys, this._keys);
        return _keys;
    }

    /**
     *  Map 
     * 
     * @returns {number}  Map 
     */
    size() {
        return this._keys.length;
    }

    /**
     *  Map 
     */
    clear() {
        this._keys = [];
        this._values = [];
    }

    /**
     *  Map 
     * @param {function} callback 
     */
    forEach(callback) {
        let keys = this.keySet();
        for (let i = 0; i < keys.length; ++i) {
            let key = keys[i];
            let value = this.get(key);
            if (callback(key, value)) {
                break;
            }
        }
    }
}
