import { Version } from "../Version";
import { Logger } from "../utils/Logger";
import { TalkService } from "../talk/TalkService";
import { NucleusTag } from "./NucleusTag";
import { NucleusConfig } from "./NucleusConfig";

/**
 *  API 
 */
export class Nucleus {
    /**
     * @param {NucleusConfig} [config] 
     */
    constructor(config) {
        /**
         * 
         * @type {NucleusConfig}
         */
        this.config = (config === undefined) ? new NucleusConfig() : config;

        /**
         * 
         * @type {Version}
         */
        this.version = new Version();

        /**
         *  
         * @type {NucleusTag}
         */
        this.tag = new NucleusTag();

        // 
        this.talkService = new TalkService(this.tag, config);
        /**
         * 
         * @type {TalkService}
         */
        this.talk = this.talkService;

        Logger.i("Nucleus", "Cell " + this.version.getNumbers() + " (Build JavaScript/Web - " + this.version.NAME + ")");
    }

    /**
     *  Nucleus 
     */
    destroy() {
        if (null != this.talkService) {
            this.talkService.hangupAll();
            this.talkService = null;
            this.talk = null;
        }

        this.config = null;
        this.version = null;
        this.tag = null;
    }

    /**
     * 
     *
     * @returns {NucleusTag} 
     */
    getTag() {
        return this.tag;
    }

    /**
     * 
     *
     * @returns {TalkService} 
     */
    getTalkService() {
        return this.talkService;
    }
}
