/**
 *  Map 
 */
export class FastMap {

    constructor() {
        // 
        this.length = 0;
        // 
        this.obj = new Object();
    }

    /**
     *  Map 
     * 
     * @returns {boolean}  {@linkcode true} 
     */
    isEmpty() {
        return this.length == 0;
    }

    /**
     * 
     * 
     * @param {string} key 
     * @returns {boolean}  {@linkcode true}  {@linkcode false} 
     */
    containsKey(key) {
        return (key in this.obj);
    }

    /**
     * 
     * 
     * @param {*} value 
     * @returns {boolean}  {@linkcode true}  {@linkcode false} 
     */
    containsValue(value) {
        for (var key in this.obj) {
            if (this.obj[key] == value) {
                return true;
            }
        }
        return false;
    }

    /**
     *  Map 
     * 
     * @param {strinig} key 
     * @param {*} value 
     */
    put(key, value) {
        if (!this.containsKey(key)) {
            this.length++;
        }
        this.obj[key] = value;
    }

    /**
     * 
     * 
     * @param {string} key 
     * @returns {*}  {@linkcode null} 
     */
    get(key) {
        return this.containsKey(key) ? this.obj[key] : null;
    }

    /**
     * 
     * 
     * @param {string} key 
     * @returns {*} 
     */
    remove(key) {
        if (this.containsKey(key)) {
            var ret = this.obj[key];
            delete this.obj[key];
            this.length--;
            return ret;
        }

        return null;
    }

    /**
     *  Map 
     * 
     * @returns {Array} 
     */
    values() {
        var _values = new Array();
        for (var key in this.obj) {
            _values.push(this.obj[key]);
        }
        return _values;
    }

    /**
     *  Map 
     * 
     * @returns {Array} 
     */
    keys() {
        var _keys = new Array();
        for (var key in this.obj) {
            _keys.push(key);
        }
        return _keys;
    }

    /**
     *  Map 
     * 
     * @returns {Array} 
     */
    keySet() {
        var _keys = new Array();
        for (var key in this.obj) {
            _keys.push(key);
        }
        return _keys;
    }

    /**
     *  Map 
     * 
     * @returns {number}  Map 
     */
    size() {
        return this.length;
    }

    /**
     *  Map 
     */
    clear() {
        this.length = 0;
        this.obj = new Object();
    }
}
