import { Speaker } from "./Speaker";
import { Primitive } from "./Primitive";
import { TalkError } from "./TalkError";

/**
 * 
 * 
 * @interface
 */
export class TalkListener {

    constructor() { }

    ctor() { }

    initializer() { }

    /**
     * 
     *
     * @param {Speaker} speaker 
     * @param {string} cellet  Cellet 
     * @param {Primitive} primitive 
     */
    onListened(speaker, cellet, primitive) { }

    /**
     * 
     *
     * @param {Speaker} speaker 
     * @param {string} cellet  Cellet 
     * @param {Primitive} primitive 
     */
    onSpoke(speaker, cellet, primitive) { }

    /**
     * 
     *
     * @param {Speaker} speaker 
     * @param {string} cellet  Cellet 
     * @param {Primitive} primitive 
     */
    onAck(speaker, cellet, primitive) { }

    /**
     * 
     *
     * @param {Speaker} speaker 
     * @param {string} cellet  Cellet 
     * @param {Primitive} primitive 
     */
    onSpeakTimeout(speaker, cellet, primitive) { }

    /**
     * 
     *
     * @param {Speaker} speaker 
     */
    onContacted(speaker) { }

    /**
     * 
     *
     * @param {Speaker} speaker 
     */
    onQuitted(speaker) { }

    /**
     * 
     *
     * @param {Speaker} speaker 
     * @param {TalkError} error 
     */
    onFailed(speaker, error) { }
}
