import cell from "@lib/cell-lib";
import InDB from "indb";
import { Contact } from "./Contact";
import { ContactService } from "./ContactService";
import { Group } from "./Group";
import { GroupAppendix } from "./GroupAppendix";

/**
 * 
 */
export class ContactStorage {

    constructor(service) {
        /**
         * 
         * @type {ContactService}
         */
        this.service = service;

        /**
         * 
         * @type {string}
         */
        this.domain = null;

        /**
         * 
         * @type {InDB}
         */
        this.db = null;

        /**
         * 
         * @type {object}
         */
        this.groupStore = null;

        /**
         * 
         * @type {object}
         */
        this.contactStore = null;

        /**
         * 
         * @type {object}
         */
        this.appendixStore = null;
    }

    /**
     * 
     * @param {number} id  ID 
     * @param {string} domain 
     */
    open(id, domain) {
        if (null != this.db) {
            return;
        }

        cell.Logger.d('ContactStorage', 'Open contact storage : ' + domain);

        this.domain = domain;

        // 
        let options = {
            name: 'CubeContact-' + domain + '-' + id,
            version: 1,
            stores: [{
                name: 'group',
                keyPath: 'id',
                indexes: [{
                    name: 'id',
                    keyPath: 'id',
                    unique: true
                }, {
                    name: 'lastActive',
                    keyPath: 'lastActive',
                    unique: false
                }, {
                    name: 'creation',
                    keyPath: 'creation',
                    unique: false
                }, {
                    name: 'state',
                    keyPath: 'state',
                    unique: false
                }]
            }, {
                name: 'contact',
                keyPath: 'id',
                indexes: [{
                    name: 'id',
                    keyPath: 'id',
                    unique: true
                }, {
                    name: 'last',
                    keyPath: 'last',
                    unique: false
                }, {
                    name: 'timestamp',
                    keyPath: 'timestamp',
                    unique: false
                }]
            }, {
                name: 'appendix',
                keyPath: 'id',
                indexes: [{
                    name: 'id',
                    keyPath: 'id',
                    unique: true
                }]
            }, {
                name: 'top',
                keyPath: 'id',
                indexes: [{
                    name: 'id',
                    keyPath: 'id',
                    unique: true
                }]
            }, {
                name: 'config',
                keyPath: 'item',
                indexes: [{
                    name: 'item',
                    keyPath: 'item',
                    unique: true
                }]
            }]
        };

        this.db = new InDB(options);

        // 
        //this.db.connect();

        this.groupStore = this.db.use('group');
        this.contactStore = this.db.use('contact');
        this.appendixStore = this.db.use('appendix');
        this.topStore = this.db.use('top');
        this.configStore = this.db.use('config');
    }

    /**
     * 
     */
    close() {
        if (null == this.db) {
            return;
        }

        cell.Logger.d('MessagingStorage', 'Close contact storage : ' + this.domain);

        this.db.close();
        this.db = null;
    }

    /**
     * 
     * @param {number} id  ID 
     * @param {function} handler ({@linkcode contact}:{@link Contact})  {@linkcode contact}  {@linkcode null} 
     */
    readContact(id, handler) {
        (async ()=> {
            let result = await this.contactStore.query('id', id);
            if (result.length > 0) {
                let data = result[0];
                let contact = Contact.create(data);
                handler(contact);
            }
            else {
                handler(null);
            }
        })();
    }

    /**
     * 
     * @param {Contact} contact 
     */
    writeContact(contact) {
        (async ()=> {
            let data = contact.toJSON();
            await this.contactStore.put(data);
        })();
    }

    /**
     *  ID 
     * @param {number} id  ID 
     * @param {function} handler ({@linkcode id}:{@linkcode number}, {@linkcode contains}:{@linkcode boolean}) - (ID, )
     * @returns {boolean} 
     */
    containsGroup(id, handler) {
        if (null == this.db) {
            return false;
        }

        (async ()=> {
            let result = await this.groupStore.query('id', id);
            if (result.length > 0) {
                handler(id, true);
            }
            else {
                handler(id, false);
            }
        })();
        return true;
    }

    /**
     * 
     * @param {number} beginning 
     * @param {number} ending 
     * @param {function} handler ({@linkcode beginning}:{@linkcode number}, {@linkcode ending}:{@linkcode number}, {@linkcode groups}:Array<{@link Group}>) 
     * @param {Array} [matchingStates] 
     * @returns {boolean} 
     */
    readGroups(beginning, ending, handler, matchingStates) {
        if (null == this.db) {
            return false;
        }

        (async ()=> {
            // let result = await this.groupStore.all();
            let result = null;

            if (undefined === matchingStates) {
                result = await this.groupStore.select([
                    { key: 'lastActive', value: beginning, compare: '>' },
                    { key: 'lastActive', value: ending, compare: '<=', optional: true }
                ]);
            }
            else {
                let conditions = [
                    { key: 'lastActive', value: beginning, compare: '>' },
                    { key: 'lastActive', value: ending, compare: '<=', optional: true }
                ];
                for (let i = 0; i < matchingStates.length; ++i) {
                    conditions.push({
                        key: 'state', value: matchingStates[i], options: true
                    });
                }
                result = await this.groupStore.select(conditions);
            }

            if (null == result || undefined === result) {
                handler(beginning, ending, []);
                return;
            }

            let selfId = this.service.getSelf().getId();
            let groups = [];
            let count = 0;
            for (let i = 0; i < result.length; ++i) {
                let json = result[i];
                if (json.lastActive > beginning && json.lastActive <= ending) {

                    if (undefined !== matchingStates) {
                        // 
                        if (matchingStates.indexOf(json.state) < 0) {
                            continue;
                        }
                    }

                    let included = false;
                    if (json.ownerId != selfId) {
                        // 
                        for (let n = 0; n < json.members.length; ++n) {
                            if (json.members[n].id == selfId) {
                                included = true;
                                break;
                            }
                        }
                    }
                    else {
                        included = true;
                    }

                    if (!included) {
                        continue;
                    }

                    json.domain = this.domain;
                    let group = Group.create(this.service, json);
                    ++count;

                    // 
                    (async (curGroup)=> {
                        // 
                        let appendixData = await this.appendixStore.query('id', curGroup.id);

                        if (appendixData.length > 0) {
                            let appendix = GroupAppendix.create(this.service, curGroup, appendixData[0]);
                            curGroup.appendix = appendix;
                        }

                        groups.push(curGroup);

                        if (count == groups.length) {
                            handler(beginning, ending, groups);
                        }
                    })(group);
                }
            }

            if (count == groups.length) {
                handler(beginning, ending, groups);
            }
        })();

        return true;
    }

    /**
     *  ID 
     * @param {number} id  ID 
     * @param {function} handler ({@linkcode id}:{@linkcode number}, {@linkcode group}:{@link Group}) - (ID, )  {@linkcode group}  {@linkcode null} 
     */
    readGroup(id, handler) {
        if (null == this.db) {
            handler(id, null);
            return;
        }

        (async ()=> {
            let result = await this.groupStore.query('id', id);
            if (null != result && result.length > 0) {
                let json = result[0];
                let group = Group.create(this.service, json);

                (async ()=> {
                    // 
                    let appendixData = await this.appendixStore.query('id', id);

                    if (appendixData.length > 0) {
                        let appendix = GroupAppendix.create(this.service, group, appendixData[0]);
                        group.appendix = appendix;
                    }

                    handler(id, group);
                })();
            }
            else {
                handler(id, null);
            }
        })();
    }

    /**
     * 
     * @param {Group} group 
     * @returns {boolean} 
     */
    writeGroup(group) {
        if (null == this.db) {
            return false;
        }

        (async ()=> {
            let data = group.toJSON();
            delete data["domain"];
            await this.groupStore.put(data);
        })();

        (async () => {
            let data = group.getAppendix().toJSON();
            await this.appendixStore.put(data);
        })();

        return true;
    }

    /**
     * 
     * @param {function} handler 
     * @returns {boolean}
     */
    readTopList(handler) {
        if (null == this.db) {
            return false;
        }

        (async ()=> {
            let result = await this.topStore.all();
            if (null != result && result.length > 0) {
                let list = [];
                result.forEach((value) => {
                    list.push(value)
                });
                handler(list);
            }
            else {
                handler([]);
            }
        })();

        return true;
    }

    /**
     * 
     * @param {number} topId 
     * @param {string} type 
     * @returns {boolean}
     */
    writeTopList(topId, type) {
        if (null == this.db) {
            return false;
        }

        (async ()=> {
            await this.topStore.put({ "id": topId, "type": type });
        })();

        return true;
    }

    /**
     * 
     * @param {number} topId 
     * @returns {boolean}
     */
    deleteTopList(topId) {
        if (null == this.db) {
            return false;
        }

        (async ()=> {
            await this.topStore.delete(topId);
        })();

        return true;
    }

    /**
     * 
     * @returns {boolean}
     */
    emptyTopList() {
        if (null == this.db) {
            return false;
        }

        (async ()=> {
            await this.topStore.clear();
        })();

        return true;
    }

    /**
     * 
     * @param {function} handler 
     * @returns {boolean}
     */
    readBlockList(handler) {
        if (null == this.db) {
            return false;
        }

        (async () => {
            let result = await this.configStore.get('blockList');
            if (null != result) {
                handler(result.list);
            }
            else {
                handler([]);
            }
        })();

        return true;
    }

    /**
     * 
     * @param {Array< number >} list 
     * @returns {boolean}
     */
    writeBlockList(list) {
        if (null == this.db) {
            return false;
        }

        (async () => {
            let data = {
                "item": "blockList",
                "list": list
            };
            await this.configStore.put(data);
        })();

        return true;
    }
}
