import cell from "@lib/cell-lib";
import { Subject } from "./Subject";
import { Kernel } from "./Kernel";
import { Pipeline } from "./Pipeline";
import { PluginSystem } from "./PluginSystem";
import { AuthToken } from "../auth/AuthToken";
import { OrderMap } from "../util/OrderMap";

/**
 * 
 * @typedef {object} ModuleEvent
 * @property {string} name 
 * @property {object} data 
 */

/**
 * 
 */
export class Module extends Subject {

    /**
     * @param {string} name 
     */
    constructor(name) {
        super();

        /**
         * 
         * @protected
         * @type {string}
         */
        this.name = name;

        /**
         * 
         * @protected
         * @type {Kernel}
         */
        this.kernel = null;

        /**
         * 
         * @protected
         * @type {Pipeline}
         */
        this.pipeline = null;

        /**
         * 
         * @protected
         * @type {boolean}
         */
        this.started = false;

        /**
         * 
         * @protected
         * @type {Array<string>}
         */
        this.dependencies = [];

        /**
         * 
         * @protected
         * @type {Array<string>}
         */
        this.deps = [];

        /**
         * 
         * @private
         * @type {OrderMap}
         */
        this.depsLoadMap = new OrderMap();

        /**
         * 
         * @private
         * @type {boolean}
         */
        this.depsLoading = false;

        /**
         * 
         * @protected
         * @type {PluginSystem}
         */
        this.pluginSystem = new PluginSystem();

        /**
         * 30
         * @protected
         * @type {number}
         */
        this.defaultRetrospect = 30 * 24 * 60 * 60000;
    }

    /**
     * 
     * @returns {string} 
     */
    getName() {
        return this.name;
    }

    /**
     * 
     * @returns {AuthToken}  {@linkcode null} 
     */
    getAuthToken() {
        return this.kernel.getAuthToken();
    }

    /**
     * 
     * @param {string} moduleName 
     */
    require(moduleName) {
        let index = this.dependencies.indexOf(moduleName);
        if (index >= 0) {
            return;
        }

        this.dependencies.push(moduleName);
    }

    /**
     * 
     * @param {string} fileOrURL  URL 
     * @param {function} [successCallback]
     * @param {function} [failureCallback]
     */
    requireFile(fileOrURL, successCallback, failureCallback) {
        let index = this.deps.indexOf(fileOrURL);
        if (index >= 0) {
            return;
        }

        this.deps.push(fileOrURL);

        this.depsLoadMap.put(fileOrURL, {
            key: fileOrURL,
            success: successCallback,
            failure: failureCallback
        });

        if (this.started) {
            if (this.depsLoading) {
                return;
            }

            this.depsLoading = true;

            this._loadDepsByOrder();
        }
    }

    /**
     * 
     * @private
     */
    _loadDepsByOrder() {
        if (this.depsLoadMap.isEmpty()) {
            this.depsLoading = false;
            return;
        }

        let fileOrURL = this.depsLoadMap.keys()[0];
        let value = this.depsLoadMap.remove(fileOrURL);
        let successCallback = value.success;
        let failureCallback = value.failure;

        let next = () => {
            this._loadDepsByOrder();
        };

        this.kernel.loadDepsJS(fileOrURL).then(() => {
            cell.Logger.i(this.getName(), 'Load deps file: ' + fileOrURL);

            if (undefined !== successCallback) {
                successCallback(fileOrURL);
            }

            next();
        }).catch(() => {
            cell.Logger.w(this.getName(), 'Can NOT load deps file: ' + fileOrURL);

            if (undefined !== failureCallback) {
                failureCallback(fileOrURL);
            }

            next();
        });
    }

    /**
     * 
     * @returns {boolean}  {@linkcode true}  {@linkcode false} 
     */
    hasStarted() {
        return this.started;
    }

    /**
     * 
     * @returns {boolean}  {@linkcode false} 
     */
    start() {
        // 
        if (!this.kernel.working) {
            return false;
        }

        // 
        if (this.started) {
            return false;
        }

        // 
        for (let i = 0; i < this.dependencies.length; ++i) {
            let mod = this.kernel.getModule(this.dependencies[i]);
            if (null != mod) {
                mod.start();
            }
        }

        this.started = true;
        return true;
    }

    /**
     * 
     */
    stop() {
        this.started = false;
        this.depsLoading = false;
        this.depsLoadMap.clear();
    }

    /**
     * 
     */
    suspend() {
        // Nothing
    }

    /**
     * 
     */
    resume() {
        // Nothing
    }

    /**
     * 
     * @param {JSON} config 
     */
    config(config) {
        // Nothing
    }

    /**
     * 
     * @returns {boolean}  {@linkcode true} 
     */
    isReady() {
        return true;
    }

    /**
     * 
     * @param {string} [event] 
     * @param {function} listener  {@link ModuleEvent} 
     * @see {@link ModuleEvent}
     */
    on(event, listener) {
        if (typeof event === 'string') {
            this.attachWithName(event, listener);
        }
        else if (typeof event === 'function') {
            this.attach(event);
        }
    }

    /**
     * 
     * @returns {PluginSystem} 
     */
    getPluginSystem() {
        return this.pluginSystem;
    }
}
