/**
 * 
 * @private
 */
export const PipelineState = {

    /**
     * 
     * @type {number}
     */
    OK: 1000,

    /**
     * 
     * @type {number}
     */
    BadRequest: 1400,

    /**
     * 
     * @type {number}
     */
    NotFound: 1404,

    /**
     * 
     * @type {number}
     */
    NoAuthToken: 1501,

    /**
     * 
     * @type {number}
     */
    SystemBusy: 1502,

    /**
     * 
     * @type {number}
     */
    ServiceTimeout: 2001,

    /**
     * 
     * @type {number}
     */
    PayloadFormat: 2002,

    /**
     * 
     * @type {number}
     */
    InvalidParameter: 2003,

    /**
     * 
     * @type {number}
     */
    GatewayError: 2101,

    /**
     * 
     * @private
     * @param {ActionDialect} action 
     * @returns  JSON  {@linkcode null} 
     */
    extractState: function(action) {
        if (action.containsParam('state')) {
            return action.getParamAsJson('state');
        }
        else {
            return null;
        }
    }
}
