import { JSONable } from "../util/JSONable";
import { PrimaryDescription } from "./PrimaryDescription";

/**
 * 
 * @extends JSONable
 */
export class AuthToken extends JSONable {

    /**
     * @param {string} code 
     * @param {string} domain 
     * @param {string} appKey  App Key 
     * @param {number} cid  Contact ID 
     * @param {number} issues 
     * @param {number} expiry 
     * @param {PrimaryDescription} description 
     */
    constructor(code, domain, appKey, cid, issues, expiry, description) {
        super();

        /**
         * 
         * @type {string}
         */
        this.code = code;

        /**
         * 
         * @type {string}
         */
        this.domain = domain;

        /**
         *  App Key 
         * @type {string}
         */
        this.appKey = appKey;

        /**
         *  Contact ID  {@linkcode 0} 
         * @type {number}
         */
        this.cid = cid;

        /**
         * 
         * @type {number}
         */
        this.issues = issues;

        /**
         * 
         * @type {number}
         */
        this.expiry = expiry;

        /**
         * 
         * @type {PrimaryDescription}
         */
        this.description = description;
    }

    /**
     * 
     * @returns {boolean}  {@linkcode true}  {@linkcode false} 
     */
    isValid() {
        return (Date.now() < this.expiry);
    }

    /**
     * 
     * @returns {PrimaryDescription} 
     */
    getDescription() {
        return this.description;
    }

    /**
     * @inheritdoc
     */
    toJSON() {
        let json = super.toJSON();
        json.code = this.code;
        json.domain = this.domain;
        json.appKey = this.appKey;
        json.cid = this.cid;
        json.issues = this.issues;
        json.expiry = this.expiry;
        json.description = this.description.toJSON();
        return json;
    }

    /**
     *  JSON  {@link AuthToken} 
     * @param {JSON} json  {@link AuthToken}  JSON 
     * @returns {AuthToken}  {@link AuthToken} 
     */
    static create(json) {
        let code = json["code"];
        let domain = json["domain"];
        let appKey = json["appKey"];
        let cid = json["cid"];
        let issues = json["issues"];
        let expiry = json["expiry"];
        let description = PrimaryDescription.create(json["description"]);
        let authToken = new AuthToken(code, domain, appKey, cid, issues, expiry, description);
        return authToken;
    }
}
