import { JSONable } from "../util/JSONable";
import { Entity } from "../core/Entity";

/**
 * 
 * @extends Entity
 */
export class AbstractContact extends Entity {

    /**
     * @param {number|string} id  ID 
     * @param {string} [name] 
     * @param {string} [domain] 
     */
    constructor(id, name, domain) {
        super(id);

         /**
          * 
          * @private
          * @type {string}
          */
        this.name = (undefined === name) ? 'Cube-' + id : name;

        /**
         * 
         * @private
         * @type {string}
         */
        this.namePY = '';
 
         /**
          * 
          * @private
          * @type {string}
          */
        this.domain = (undefined === domain) ? AuthService.DOMAIN : domain;
    }

    /**
     *  ID 
     * @returns {number}  ID 
     */
    getId() {
        return this.id;
    }

    /**
     * 
     * @returns {string} 
     */
    getDomain() {
        return this.domain;
    }

    /**
     * 
     * @param {string} name 
     */
    setName(name) {
        this.name = name;
    }

    /**
     * 
     * @returns {string} 
     */
    getName() {
        return this.name;
    }

    /**
     * 
     * @returns {string} 
     */
    getNamePinYin() {
        let array = this.namePY.split(',');
        return array.join('');
    }

    /**
     * @inheritdoc
     */
    toJSON() {
        let json = super.toJSON();
        json.name = this.name;
        json.domain = this.domain;

        json.namePY = this.namePY;

        if (null != this.context) {
            json.context = (this.context instanceof JSONable) ? this.context.toJSON() : this.context;
        }

        return json;
    }

    /**
     *  JSON 
     * @returns {JSON}  JSON 
     */
    toCompactJSON() {
        let json = super.toCompactJSON();
        json.name = this.name;
        json.domain = this.domain;

        json.namePY = this.namePY;

        if (null != this.context) {
            json.context = (this.context instanceof JSONable) ? this.context.toJSON() : this.context;
        }

        return json;
    }
}
