import cell from "@lib/cell-lib";
import { Entity } from "../core/Entity";
import { MessageState } from "./MessageState";
import { AuthService } from "../auth/AuthService";
import { FileLabel } from "../filestorage/FileLabel";
import { FileAttachment } from "./FileAttachment";
import { Contact } from "../contact/Contact";
import { Group } from "../contact/Group";

/**
 * 
 * @extends Entity
 */
export class Message extends Entity {

    /**
     * @param {JSON|Message|File|FileLabel} payload 
     * @param {File|FileLabel} [file] 
     */
    constructor(payload, file) {
        super();

        // 
        let cloning = (undefined !== payload && payload instanceof Message);

        /**
         *  ID 
         * @type {number}
         */
        this.id = cloning ? payload.id : cell.Utils.generateSerialNumber();

        /**
         * 
         * @private
         * @type {string}
         */
        this.domain = cloning ? payload.domain : AuthService.DOMAIN;

        /**
         * 
         * @type {JSON}
         */
        this.payload = null;

        if (cloning) {
            this.payload = payload.payload;
        }
        else {
            if (undefined !== payload && null != payload && !(payload instanceof File) && !(payload instanceof FileLabel)) {
                this.payload = payload;
            }
            else {
                this.payload = {};
            }
        }

        /**
         *  ID 
         * @type {number}
         */
        this.from = cloning ? payload.from : 0;

        /**
         * 
         * @type {Contact}
         */
        this.sender = cloning ? payload.sender : null;

        /**
         *  ID 
         * @type {number}
         */
        this.to = cloning ? payload.to : 0;

        /**
         * 
         * @type {Contact}
         */
        this.receiver = cloning ? payload.receiver : null;

        /**
         *  ID 
         * @type {number}
         */
        this.source = cloning ? payload.source : 0;

        /**
         * 
         * @type {Group}
         */
        this.sourecGroup = cloning ? payload.sourecGroup : null;

        /**
         *  ID 
         * @type {number}
         */
        this.owner = cloning ? payload.owner : 0;

        /**
         * 
         * @type {number}
         */
        this.localTS = cloning ? payload.localTS : 0;

        /**
         * 
         * @type {number}
         */
        this.remoteTS = cloning ? payload.remoteTS : 0;

        /**
         * 
         * @type {FileAttachment}
         */
        this.attachment = null;

        if (cloning) {
            this.attachment = payload.attachment;
        }
        else if (payload instanceof File) {
            this.attachment = new FileAttachment(payload);
        }
        else if (payload instanceof FileLabel) {
            this.attachment = new FileAttachment(payload);
        }
        else if (undefined !== file && null != file) {
            if (file instanceof File) {
                this.attachment = new FileAttachment(file);
            }
            else if (file instanceof FileLabel) {
                this.attachment = new FileAttachment(file);
            }
        }

        /**
         * 
         * @type {string}
         */
        this.summary = '';

        /**
         * 
         * @type {number}
         * @see MessageState
         */
        this.state = cloning ? payload.state : MessageState.Unknown;

        /**
         * 
         * 0 - 
         * 1 - 
         * @private
         * @type {number}
         * @see MessageScope
         */
        this.scope = cloning ? payload.scope : 0;
    }

    /**
     * 
     * @returns {string} 
     */
    getDomain() {
        return this.domain;
    }

    /**
     *  ID 
     * @returns {number}  ID 
     */
    getFrom() {
        return this.from;
    }

    /**
     * 
     * @returns {Contact} 
     */
    getSender() {
        return this.sender;
    }

    /**
     *  ID 
     * @returns {number}  ID 
     */
    getTo() {
        return this.to;
    }

    /**
     * 
     * @returns {Contact} 
     */
    getReceiver() {
        return this.receiver;
    }

    /**
     *  ID 
     * @returns {number}
     */
    getSource() {
        return this.source;
    }

    /**
     * 
     * @returns {Group} 
     */
    getSourceGroup() {
        return this.sourecGroup;
    }

    /**
     * 
     * @returns {JSON} 
     */
    getPayload() {
        return this.payload;
    }

    /**
     * 
     * @param {JSON} payload 
     */
    setPayload(payload) {
        this.payload = payload;
    }

    /**
     * 
     * @returns {number} 
     */
    getLocalTimestamp() {
        return this.localTS;
    }

    /**
     * 
     * @returns {number} 
     */
    getRemoteTimestamp() {
        return this.remoteTS;
    }

    /**
     * 
     * @returns {string} 
     */
    getSummary() {
        return this.summary;
    }

    /**
     * 
     * @returns {number} 
     * @see MessageState
     */
    getState() {
        return this.state;
    }

    /**
     * 
     */
    markOnlyOwner() {
        this.scope = 1;
    }

    /**
     * 
     * @returns {boolean}  {@linkcode true} 
     */
    isRead() {
        if (this.owner == 0 || this.from == this.owner) {
            return true;
        }

        return (this.state == MessageState.Read);
    }

    /**
     * 
     * @param {File|FileAttachment} file 
     */
    setAttachment(file) {
        if (file instanceof File) {
            this.attachment = new FileAttachment(file);
        }
        else if (file instanceof FileAttachment) {
            this.attachment = file;
        }
    }

    /**
     * 
     * @returns {FileAttachment} 
     */
    getAttachment() {
        return this.attachment;
    }

    /**
     * 
     * @returns {boolean}  {@linkcode true}  {@linkcode false} 
     */
    hasAttachment() {
        return (null != this.attachment);
    }

    /**
     * 
     * @returns {number}  {@linkcode true}  {@linkcode false} 
     */
    isFromGroup() {
        return this.source > 0;
    }

    /**
     *  ID 
     * @returns {number}  ID 
     */
    getGroupId() {
        return this.source;
    }

    /**
     * 
     * @protected
     * @param {Message} src 
     */
    clone(src) {
        this.id = src.id;
        this.domain = src.domain;
        this.from = src.from;
        this.to = src.to;
        this.source = src.source;
        this.owner = src.owner;
        this.localTS = src.localTS;
        this.remoteTS = src.remoteTS;
        this.state = src.state;
        this.scope = src.scope;
        this.payload = src.payload;
        this.summary = src.summary;
        this.attachment = src.attachment;
    }

    /**
     * @private
     * @param {number} owner 
     * @param {number} to 
     * @param {number} source 
     */
    _assign(owner, to, source) {
        this.owner = owner;
        this.from = owner;
        this.to = to;
        this.source = source;
        this.localTS = this.timestamp;
        this.remoteTS = this.localTS;
    }

    /**
     * @inheritdoc
     */
    toJSON() {
        let json = super.toJSON();
        json["id"] = this.id;
        json["domain"] = this.domain;
        json["from"] = this.from;
        json["to"] = this.to;
        json["source"] = this.source;
        json["owner"] = this.owner;
        json["lts"] = this.localTS;
        json["rts"] = this.remoteTS;
        json["state"] = this.state;
        json["scope"] = this.scope;
        json["payload"] = this.payload;

        json["summary"] = this.summary;

        if (null != this.attachment) {
            json["attachment"] = this.attachment.toJSON();
        }

        return json;
    }

    /**
     *  JSON  {@link Message} 
     * @private
     * @param {JSON} json  JSON 
     * @returns {Message}  {@link Message} 
     */
    static create(json) {
        let message = new Message();
        message.id = json.id;
        message.domain = json.domain;
        message.from = json.from;
        message.to = json.to;
        message.source = json.source;
        message.owner = json.owner;
        message.localTS = json.lts;
        message.remoteTS = json.rts;
        message.state = json.state;
        message.scope = json.scope;

        if (json.payload !== undefined) {
            message.payload = json.payload;
        }

        if (json.summary !== undefined) {
            message.summary = json.summary;
        }

        if (json.attachment !== undefined) {
            message.attachment = FileAttachment.create(json.attachment);
        }

        return message;
    }
}
