import { Utils } from "../utils/Utils";
import { Logger } from "../utils/Logger";
import pako from "pako";

/**
 * 
 */
export class Message {
    /**
     * @param {Array|Uint8Array} payload 
     */
    constructor(payload) {
        /**
         * 
         * @type {Array|Uint8Array}
         */
        this.payload = payload;

        /**
         * 
         * @type {object}
         */
        this.context = null;

        /**
         * 
         * @private
         * @type {boolean}
         */
        this.compressible = false;

        // 
        this.verifyCompression();
    }

    /**
     * 
     *
     * @param {Array|Uint8Array} payload 
     */
    setPayload(payload) {
        this.payload = payload;
    }

    /**
     * 
     *
     * @param {object} context 
     */
    setContext(context) {
        this.context = context;
    }

    /**
     * 
     *
     * @returns {Array|Uint8Array} 
     */
    getPayload() {
        return this.payload;
    }

    /**
     * 
     * 
     * @returns {object} 
     */
    getContext() {
        return this.context;
    }

    /**
     * 
     *
     * @private
     * @returns {boolean} 
     */
    verifyCompression() {
        if ((this.payload[0] == 120 && this.payload[1] == -100)
            || this.payload[0] == 120 && this.payload[1] == 156) {
            this.compressible = true;
        }
        else {
            this.compressible = false;
        }
        return this.compressible;
    }

    /**
     * 
     * 
     * @returns {boolean}  {@linkcode true} 
     */
    needCompressible() {
        return (this.payload.length >= Message.CompressionThreshold);
    }

    /**
     * 
     * 
     * @param {Array|Uint8Array} data 
     * @param {Array} key 
     * @returns {Array|Uint8Array} 
     */
    static pack(data, key) {
        var output = null;

        // 
        var buf = null;
        if (null != key) {
            buf = Utils.simpleEncrypt(data, key);
        }
        else {
            buf = data;
        }

        // 
        if (buf.length > Message.CompressionThreshold) {
            output = Message.compress(buf);
        }
        else {
            output = buf;
        }

        return output;
    }

    /**
     * 
     * 
     * @param {Array|Uint8Array} data 
     * @param {Array} key 
     * @returns {Array|Uint8Array} 
     */
    static unpack(data, key) {
        var output = null;

        // 
        var buf = null;
        if (data[0] == 120 && data[1] == 156) {
            buf = Message.uncompress(data);
        }
        else {
            buf = data;
        }

        if (null == buf) {
            // 
            return null;
        }

        // 
        if (null != key) {
            output = Utils.simpleDecrypt(buf, key);
        }
        else {
            output = buf;
        }

        return output;
    }

    /**
     * 
     * 
     * @private
     * @param {Uint8Array} input 
     * @returns {Uint8Array}
     */
    static compress(input) {
        return pako.deflate(input);
    }

    /**
     * 
     * 
     * @private
     * @param {Uint8Array} input 
     * @returns {Uint8Array}
     */
    static uncompress(input) {
        var result = null;
        try {
            result = pako.inflate(input);
        } catch (err) {
            Logger.e("Message", "Uncompress error: " + err);
        }
        return result;
    }
}

/**
 * 
 * @type {array}
 */
Message.DefaultHead = [ 0x20, 0x13, 0x09, 0x08 ];

/**
 * 
 * @type {array}
 */
Message.DefaultTail = [ 0x19, 0x78, 0x10, 0x04 ];

/**
 * 
 *  120 
 * @type {number}
 * @default 1024
 */
Message.CompressionThreshold = 1024;
