import { ModuleError } from "../core/error/ModuleError";
import { OrderMap } from "../util/OrderMap";
import { FileAnchor } from "./FileAnchor";
import { FileHierarchy } from "./FileHierarchy";
import { FileLabel } from "./FileLabel";
import { FileStorage } from "./FileStorage";
import { FileStorageState } from "./FileStorageState";

/**
 * 
 */
export class Directory {

    /**
     * @param {FileHierarchy} hierarchy 
     */
    constructor(hierarchy) {
        /**
         * 
         * @type {FileHierarchy}
         */
        this.hierarchy = hierarchy;

        /**
         *  ID 
         * @type {number}
         */
        this.id = 0;

        /**
         * 
         * @type {string}
         */
        this.domain = null;

        /**
         *  ID 
         * @type {number}
         */
        this.ownerId = 0;

        /**
         *  ID 
         * @type {number}
         */
        this.parentId = 0;

        /**
         * 
         * @type {Directory}
         */
        this.parent = null;

        /**
         * 
         * @type {string}
         */
        this.name = null;

        /**
         * 
         * @type {number}
         */
        this.creation = 0;

        /**
         * 
         * @type {number}
         */
        this.lastModified = 0;

        /**
         * 
         * @type {boolean}
         */
        this.hidden = false;

        /**
         * 
         * @type {number}
         */
        this.size = 0;

        /**
         * 
         * @type {number}
         */
        this.numDirs = 0;

        /**
         * 
         * @type {OrderMap<number,Directory>}
         */
        this.children = new OrderMap();

        /**
         * 
         * @type {number}
         */
        this.numFiles = 0;

        /**
         * 
         * @type {OrderMap<string,FileLabel>}
         */
        this.files = new OrderMap();
    }

    /**
     *  ID 
     * @returns {number}  ID 
     */
    getId() {
        return this.id;
    }

    /**
     * 
     * @returns {string} 
     */
    getDomain() {
        return this.domain;
    }

    /**
     * 
     * @returns {Directory} 
     */
    getParent() {
        return this.parent;
    }

    /**
     * 
     * @returns {boolean}  {@linkcode true} 
     */
    isRoot() {
        return (null == this.parent);
    }

    /**
     * 
     * @returns {string} 
     */
    getName() {
        return this.name;
    }

    /**
     * 
     * @returns {number} 
     */
    getCreation() {
        return this.creation;
    }

    /**
     * 
     * @returns {number} 
     */
    getLastModified() {
        return this.lastModified;
    }

    /**
     * 
     * @returns {number} 
     */
    getSize() {
        return this.size;
    }

    /**
     * 
     * @returns {boolean}  {@linkcode true} 
     */
    isHidden() {
        return this.hidden;
    }

    /**
     * 
     * @returns {number} 
     */
    totalDirs() {
        return this.numDirs;
    }

    /**
     * 
     * @returns {number} 
     */
    totalFiles() {
        return this.numFiles;
    }

    /**
     * 
     * @param {function} handleSuccess ({@linkcode dir}:{@link Directory}, {@linkcode list}:{@link Array})
     * @param {function} [handleFailure] ({@linkcode error}:{@link ModuleError})
     */
    listDirectories(handleSuccess, handleFailure) {
        if (this.numDirs == this.children.size()) {
            handleSuccess(this, this.children.values());
            return;
        }

        if (null == this.hierarchy || undefined === this.hierarchy) {
            handleSuccess(this, []);
            return;
        }

        this.hierarchy.listDirs(this, (dir) => {
            handleSuccess(this, this.children.values());
        }, (error) => {
            console.debug(error.toString());

            if (handleFailure) {
                handleFailure(error);
            }
        });
    }

    /**
     *  ID 
     * @param {number|string} id  ID 
     * @returns {Directory}  ID 
     */
    getDirectory(id) {
        return this.children.get(parseInt(id));
    }

    /**
     * 
     * @param {string} name 
     * @returns {Directory} 
     */
    getDirectoryByName(name) {
        let list = this.children.values();
        for (let i = 0; i < list.length; ++i) {
            let dir = list[i];
            if (dir.name == name) {
                return dir;
            }
        }
        return null;
    }

    /**
     * 
     * @param {File} file 
     * @param {function} handleStart ({@linkcode fileAnchor}:{@link FileAnchor}) 
     * @param {function} handleProcessing ({@linkcode fileAnchor}:{@link FileAnchor}) 
     * @param {function} handleSuccess ({@linkcode directory}:{@link Directory}, {@linkcode fileLabel}:{@link FileLabel}) 
     * @param {function} handleFailure ({@linkcode error}:{@link ModuleError}) 
     * @returns {FileAnchor} 
     */
    uploadFile(file, handleStart, handleProcessing, handleSuccess, handleFailure) {
        return this.hierarchy.uploadFile(file, this, handleStart, handleProcessing, handleSuccess, handleFailure);
    }

    /**
     * 
     * @param {function} handleStart ({@linkcode fileAnchor}:{@link FileAnchor}) 
     * @param {function} handleProcessing ({@linkcode fileAnchor}:{@link FileAnchor}) 
     * @param {function} handleSuccess ({@linkcode directory}:{@link Directory}, {@linkcode fileLabel}:{@link FileLabel}) 
     * @param {function} handleFailure ({@linkcode error}:{@link ModuleError}) 
     */
    uploadFileWithSelector(handleStart, handleProcessing, handleSuccess, handleFailure) {
        this.hierarchy.uploadFile(null, this, handleStart, handleProcessing, handleSuccess, handleFailure);
    }

    /**
     * 
     * @param {number} beginIndex 
     * @param {number} endIndex 
     * @param {function} handleSuccess ({@linkcode dir}:{@link Directory}, {@linkcode list}:{@linkcode Array}, {@linkcode begin}:{@linkcode number}, {@linkcode end}:{@linkcode number}) 
     * @param {function} [handleFailure] ({@linkcode error}:{@link ModuleError}) 
     */
    listFiles(beginIndex, endIndex, handleSuccess, handleFailure) {
        if (this.numFiles == this.files.size()) {
            let begin = beginIndex;
            let end = endIndex;

            if (end + 1 > this.numFiles) {
                end = this.numFiles;
            }

            if (begin >= end) {
                handleSuccess(this, []);
                return;
            }

            let list = this.files.values().slice(begin, end);
            handleSuccess(this, list, beginIndex, endIndex);
            return;
        }

        if (null == this.hierarchy || undefined === this.hierarchy) {
            handleSuccess(this, [], beginIndex, endIndex);
            return;
        }

        this.hierarchy.listFiles(this, beginIndex, endIndex, (dir, files, begin, end) => {
            handleSuccess(dir, files, begin, end);
        }, (error) => {
            if (handleFailure) {
                handleFailure(error);
            }
        });
    }

    /**
     *  ID 
     * @param {string|number} fileCodeOrId  ID 
     * @returns {FileLabel} 
     */
    getFile(fileCodeOrId) {
        if (typeof fileCodeOrId === 'string') {
            return this.files.get(fileCodeOrId);
        }
        else {
            let list = this.files.values();
            for (let i = 0; i < list.length; ++i) {
                let file = list[i];
                if (file.id == fileCodeOrId) {
                    return file;
                }
            }

            return null;
        }
    }

    /**
     * 
     * @param {string} name 
     * @returns {FileLabel} 
     */
    getFileByName(name) {
        let list = this.files.values();
        for (let i = 0; i < list.length; ++i) {
            let file = list[i];
            if (file.fileName == name) {
                return file;
            }
        }

        return null;
    }

    /**
     * 
     * @param {string} dirName 
     * @param {function} handleSuccess ({@linkcode newDir}:{@link Directory}) 
     * @param {function} [handleFailure] ({@linkcode error}:{@link ModuleError}) 
     */
    newDirectory(dirName, handleSuccess, handleFailure) {
        this.hierarchy.newDirectory(this, dirName, handleSuccess, handleFailure);
    }

    /**
     * 
     * @param {Array|Directory|number|string} dirs 
     * @param {boolean} recursive 
     * @param {function} handleSuccess ({@linkcode workingDir}:{@link Directory}, {@linkcode deletedList}:{@linkcode Array<Directory>}) 
     * @param {function} [handleFailure] ({@linkcode error}:{@link ModuleError}) 
     */
    deleteDirectory(dirs, recursive, handleSuccess, handleFailure) {
        this.hierarchy.deleteDirectory(this, dirs, recursive, handleSuccess, handleFailure);
    }

    /**
     * 
     * @param {string} newName 
     * @param {function} handleSuccess ({@linkcode workingDir}:{@link Directory}) 
     * @param {function} handleFailure ({@linkcode error}:{@link ModuleError}) 
     */
    rename(newName, handleSuccess, handleFailure) {
        if (this.getName() == newName) {
            if (handleFailure) {
                let error = new ModuleError(FileStorage.NAME, FileStorageState.DuplicationOfName, this);
                handleFailure(error);
            }
            return;
        }

        this.hierarchy.renameDirectory(this, newName, handleSuccess, handleFailure);
    }

    /**
     * 
     * @param {Array} files 
     * @param {function} handleSuccess ({@linkcode workingDir}:{@link Directory}, {@linkcode deletedList}:{@linkcode Array<FileLabel>}) 
     * @param {function} [handleFailure] ({@linkcode error}:{@link ModuleError}) 
     */
    deleteFiles(files, handleSuccess, handleFailure) {
        let fileCodes = [];
        files.forEach((value) => {
            if (typeof value === 'number') {
                fileCodes.push(this.getFile(value).getFileCode());
            }
            else {
                fileCodes.push(value);
            }
        });

        this.hierarchy.deleteFiles(this, fileCodes, handleSuccess, handleFailure);
    }

    /**
     * 
     * @param {SearchFilter} filter 
     * @param {function} handleSuccess ({@linkcode filter}:{@link SearchFilter}, {@linkcode list}:{@linkcode Array<SearchItem>}) 
     * @param {function} [handleFailure] ({@linkcode error}:{@link ModuleError}) 
     */
    searchFile(filter, handleSuccess, handleFailure) {
        this.hierarchy.searchFile(filter, handleSuccess, handleFailure);
    }

    /**
     * @private
     * @param {Directory} child 
     */
    addChild(child) {
        if (this.children.containsKey(child.id)) {
            this.removeChild(child);
        }

        child.parent = this;
        this.children.put(child.id, child);

        if (this.hierarchy) {
            this.hierarchy.dirMap.put(child.id, child);
        }
    }

    /**
     * @private
     * @param {Directory|number} child 
     */
    removeChild(child) {
        let id = (child instanceof Directory) ? child.id : child;
        if (null != this.children.remove(id)) {
            child.parent = null;
        }
    }

    /**
     * @private
     * @param {FileLabel} fileLabel 
     */
    addFile(fileLabel) {
        if (this.files.containsKey(fileLabel.getFileCode())) {
            return;
        }

        this.files.put(fileLabel.getFileCode(), fileLabel);
    }

    /**
     * @private
     * @param {FileLabel} fileLabel 
     */
    removeFile(fileLabel) {
        this.files.remove(fileLabel.getFileCode());
    }

    /**
     * @private
     * @param {FileLabel} fileLabel 
     */
    updateFile(fileLabel) {
        this.files.remove(fileLabel.getFileCode());
        this.files.put(fileLabel.getFileCode(), fileLabel);
    }

    /**
     * @private
     * @param {object} json 
     */
    update(json) {
        this.name = json.name;
        this.lastModified = json.lastModified;
        this.size = json.size;
        this.hidden = json.hidden;
        this.numDirs = json.numDirs;
        this.numFiles = json.numFiles;
    }

    /**
     *  JSON  {@link Directory} 
     * @private
     * @param {JSON} json 
     * @param {FileHierarchy} hierarchy
     * @returns {Directory} 
     */
    static create(json, hierarchy) {
        let dir = new Directory(hierarchy);
        dir.ownerId = json.owner;
        dir.id = json.id;
        dir.domain = json.domain;
        dir.name = json.name;
        dir.creation = json.creation;
        dir.lastModified = json.lastModified;
        dir.size = json.size;
        dir.hidden = json.hidden;
        dir.numDirs = json.numDirs;
        dir.numFiles = json.numFiles;

        if (undefined !== json.parentId) {
            dir.parentId = json.parentId;
        }

        if (undefined !== json.dirs) {
            for (let i = 0; i < json.dirs.length; ++i) {
                let subdir = Directory.create(json.dirs[i], hierarchy);
                dir.addChild(subdir);
            }
        }

        return dir;
    }
}
