import { JSONable } from "../util/JSONable";
import { BroswerUtil } from "../util/BroswerUtil";

/**
 * 
 * @extends JSONable
 */
export class Device extends JSONable {

    /**
     * 
     */
    static DEFAULT = new Device('Web');

    /**
     * 
     * @param {string} name 
     * @param {string} [platform] 
     */
    constructor(name, platform) {
        super();

        /**
         * 
         * @type {string}
         */
        this.name = name;

        /**
         * 
         * @type {string}
         */
        this.platform = null;

        if (undefined === platform) {
            // Chrome/86.0/Windows 10  Firefox/106.0/Mac OS X 10.5
            this.platform = BroswerUtil.getBrowserName() + '/' + BroswerUtil.getBrowserVersion() + '/' + BroswerUtil.getSystem();
        }
        else {
            this.platform = platform;
        }
    }

    /**
     * 
     * @returns {string} 
     */
    getName() {
        return this.name;
    }

    /**
     * 
     * @returns {string} 
     */
    getPlatform() {
        return this.platform;
    }

    /**
     * 
     * @param {string} platform 
     */
    setPlatform(platform) {
        this.platform = platform;
    }

    /**
     * @inheritdoc
     */
    toJSON() {
        let json = super.toJSON();
        json["name"] = this.name;
        json["platform"] = this.platform;
        return json;
    }

    /**
     * @inheritdoc
     */
    toCompactJSON() {
        return this.toJSON();
    }

    /**
     *  JSON 
     * @param {JSON} data  JSON 
     * @returns {Device} 
     */
    static create(data) {
        return new Device(data["name"], data["platform"]);
    }
}
