import { Primitive } from "../Primitive";
import { Stuff } from "../Stuff";
import { LiteralBase } from "../LiteralBase";

/**
 *  Action 
 */
export class ActionDialect extends Primitive {

    /**
     * @param {string|Primitive} mix 
     */
    constructor(mix) {
        super();
        if (typeof mix === 'string') {
            this.stuffList.push(new Stuff(mix.toString(), LiteralBase.STRING));
        }
        else {
            this.sn = mix.sn;
            this.stuffList = mix.stuffList;
        }
    }

    /**
     * 
     *
     * @param {string} name 
     */
    setName(name) {
        this.stuffList[0] = new Stuff(name, LiteralBase.STRING);
    }

    /**
     * 
     *
     * @returns {string} 
     */
    getName() {
        return this.stuffList[0].getValueAsString();
    }

    /**
     * 
     *
     * @returns {number} 
     */
    numParams() {
        return parseInt((this.stuffList.length - 1) / 2);
    }

    /**
     * 
     *
     * @param key 
     * @returns {boolean}  {@linkcode true} 
     */
    containsParam(key) {
        for (var i = 1, size = this.stuffList.length; i < size; i += 2) {
            var sk = this.stuffList[i].getValueAsString();
            if (sk == key) {
                return true;
            }
        }
        return false;
    }

    /**
     * 
     *
     * @param {string} key 
     * @param {*} value 
     * @param {number} [literal] 
     * @see LiteralBase
     */
    addParam(key, value, literal) {
        var index = this.keyIndex(key);
        if (index > 0) {
            this.stuffList[index + 1] = new Stuff(value, literal);
        }
        else {
            this.stuffList.push(new Stuff(key, LiteralBase.STRING));
            this.stuffList.push(new Stuff(value, literal));
        }
    }

    /**
     * 
     *
     * @param {string} key 
     * @returns {*}  {@linkcode null} 
     */
    getParam(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getLiteralValue();
    }

    /**
     * 
     *
     * @param {string} key 
     * @returns {string}  {@linkcode null} 
     */
    getParamAsString(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getValueAsString();
    }

    /**
     * 
     *
     * @param {string} key 
     * @returns {number}  {@linkcode null} 
     */
    getParamAsInt(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getValueAsInt();
    }

    /**
     * 
     *
     * @param {string} key 
     * @returns {number}  {@linkcode null} 
     */
    getParamAsLong(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getValueAsLong();
    }

    /**
     * 
     * 
     * @param {string} key 
     * @returns {number}  {@linkcode null} 
     */
    getParamAsFloat(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getValueAsFloat();
    }

    /**
     * 
     * 
     * @param {string} key 
     * @returns {number}  {@linkcode null} 
     */
    getParamAsDouble(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getValueAsDouble();
    }

    /**
     * 
     *
     * @param {string} key 
     * @returns {boolean}  {@linkcode null} 
     */
    getParamAsBool(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getValueAsBool();
    }

    /**
     *  JSON 
     *
     * @param {string} key 
     * @returns {object}  JSON  {@linkcode null} 
     */
    getParamAsJson(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getValueAsJson();
    }

    /**
     *  Stuff 
     * 
     * @param {string} key 
     * @returns {Stuff}  Stuff  {@linkcode null} 
     */
    getValueStuff(key) {
        for (var i = 1, size = this.stuffList.length; i < size; i += 2) {
            var sk = this.stuffList[i].getValueAsString();
            if (sk == key) {
                return this.stuffList[i + 1];
            }
        }
        return null;
    }

    /**
     *  Stuff List 
     *
     * @private
     * @param {string} key 
     * @returns {number} 
     */
    keyIndex(key) {
        for (var i = 1, size = this.stuffList.length; i < size; i += 2) {
            var sk = this.stuffList[i].getValueAsString();
            if (sk == key) {
                return i;
            }
        }
        return -1;
    }
}
